.div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #000000;
}

.h1 {
  font-family: 'Kanit', sans-serif;
  font-size: 5rem;
  font-weight: 400;
  color: #fff;
  margin: 0px;
  animation-name: parpadeo;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  
  -webkit-animation-name:parpadeo;
  -webkit-animation-duration: 1s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
}

.h2 {
  font-family: 'Kanit', sans-serif;
  font-size: 4rem;
  font-weight: 400;
  color: #fff;
  margin-top: 0px;
  margin-bottom: 25px;
  animation-name: parpadeo;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  
  -webkit-animation-name:parpadeo;
  -webkit-animation-duration: 1s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
}


@-moz-keyframes parpadeo{  
  0% { opacity: 1.0; }
  50% { opacity: 0.0; }
  100% { opacity: 1.0; }
}

@-webkit-keyframes parpadeo {  
  0% { opacity: 1.0; }
  50% { opacity: 0.0; }
   100% { opacity: 1.0; }
}

@keyframes parpadeo {  
  0% { opacity: 1.0; }
   50% { opacity: 0.0; }
  100% { opacity: 1.0; }
}

.button {
  font-family: 'Kanit', sans-serif;
  font-size: 1rem;
  font-weight: 400;
  color: rgb(0, 0, 0);
  height: 2.3rem;
  width: 8rem;
  background-color: rgb(247, 208, 44);
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem;
  margin: 0.5rem;
  text-decoration: none;
  cursor: pointer;
  transition: 0.3s;
}

.button:hover {
  background-color:rgb(214, 179, 39);
  color: rgb(0, 0, 0);
  transform: translateY(-1px);
}