.card .circle{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 80%;
}

.card .circle::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url('https://upload.wikimedia.org/wikipedia/commons/5/53/Pok%C3%A9_Ball_icon.svg');
  background-size: 280px;
  background-position: center;
  background-repeat: no-repeat;
  -webkit-clip-path: circle(120px at center);
          clip-path: circle(120px at center);
  transition: 0.8s;
}


.card:hover .circle:before{
  -webkit-transform: translateX(-500px)rotate(400deg)translateY(-200px);
          transform: translateX(-500px)rotate(400deg)translateY(-200px);
  background-image: url(/static/media/pokepoke.95b4b5a8.png);
  background-size: 400px;
  -webkit-clip-path: circle(400px at center);
          clip-path: circle(400px at center);
  width: 1000px;
  z-index: 500;
}


.card img{
  position: absolute;
  left: 34%;
  height: 300px;
  pointer-events: none;
  transition: 0.5s;
  -webkit-filter: drop-shadow(-2px 4px 15px rgb(0, 0, 0));
          filter: drop-shadow(-2px 4px 15px rgb(0, 0, 0));
  -webkit-animation: animar 2s infinite ease;
          animation: animar 2s infinite ease;
}

@-webkit-keyframes animar {
  25% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  50%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  75%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
}

@keyframes animar {
  25% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  50%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  75%{
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
}

.card:hover img {
  left:50%;
  height: 500px; 
}

.card .content{
  position: relative;
  width: 50%;
  left: 20%;
  padding: 20px 20px 20px 40px;
